import RestaurantCard from './RestaurantCard';
import TabsHeadingSection from './TabsHeadingSection';
import { useState } from 'react';

const sampleRestaurants = [
  {
    name: 'Soul Delight Kitchen',
    image: '/images/restaurant1.jpg',
    rating: 4.7,
    location: 'Harlem, NY',
    tagline: 'Authentic Soul Food & Vibes',
    type: 'restaurant',
  },
  {
    name: 'Fresh Farm Goods',
    image: '/images/farm1.jpg',
    rating: 4.6,
    location: 'Upstate NY',
    tagline: 'Farm to Table Freshness',
    type: 'farm',
  },
  {
    name: 'Urban Vegan Market',
    image: '/images/shop1.jpg',
    rating: 4.8,
    location: 'Brooklyn, NY',
    tagline: 'Plant-Based Groceries',
    type: 'shop',
  },
];


export default function FoodsAndRestaurantsPage() {
  const [selectedTab, setSelectedTab] = useState('Food from Restaurant');

  const filteredRestaurants = sampleRestaurants.filter((r) => {
    if (selectedTab === 'Food from Shop') return r.type === 'shop';
    if (selectedTab === 'Food from Farm') return r.type === 'farm';
    return r.type === 'restaurant';
  });

  return (
    <div className="p-6 max-w-7xl mx-auto">
      <TabsHeadingSection
        selected={selectedTab}
        onTabChange={setSelectedTab}
      />

      <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-6">
        {filteredRestaurants.map((rest) => (
          <RestaurantCard key={rest.name} {...rest} />
        ))}
      </div>
    </div>
  );
}
